.block_proekt_art6__navigation {
  position: absolute;
  top: 50%;
  left: 0;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  transform: translateY(-50%);
  pointer-events: none;
}

.block_proekt_art6__prev,
.block_proekt_art6__next {
  position: relative;
  display: inline-block;
  width: 42px;
  height: 42px;
  background-color: #0045a3;
  border: none;
  padding: 0;
  appearance: none;
  outline: none;
  cursor: pointer;
  transition: background-color 0.2s ease-out;
  pointer-events: all;
}
@media (hover: hover) {
  .block_proekt_art6__prev:hover,
  .block_proekt_art6__next:hover {
    background-color: #002f70;
  }
}
.block_proekt_art6__prev:disabled, .block_proekt_art6__prev.swiper-button-disabled,
.block_proekt_art6__next:disabled,
.block_proekt_art6__next.swiper-button-disabled {
  opacity: 0;
  cursor: default;
}
.block_proekt_art6__prev::after,
.block_proekt_art6__next::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  background-repeat: no-repeat;
  background-position: cente;
  background-size: cover;
}
@media (max-width: 1300px) {
  .block_proekt_art6__prev,
  .block_proekt_art6__next {
    width: 36px;
    height: 36px;
  }
}
